function sliderAdvantages() {
    var swiper = new Swiper('.advantages .swiper', {
        slidesPerView: '4',
        spaceBetween: 30,
        pagination: {
            el: ".advantages .swiper-pagination",
            clickable: true,
        },
        breakpoints: {
            320: {
                slidesPerView: 1,
                spaceBetween: 20,
            },
            577: {
                slidesPerView: 2,
                spaceBetween: 20,
            },
            769: {
                slidesPerView: 3,
                spaceBetween: 20,
            },
            993: {
                slidesPerView: 4,
                spaceBetween: 30,
            },
        }
    })
}
function sliderFeedback() {
    var swiper = new Swiper('.feedback .swiper', {
        slidesPerView: 'auto',
        spaceBetween: 65,
        slideToClickedSlide: true,
        navigation: {
            nextEl: '.feedback .swiper-button-next',
            prevEl: '.feedback .swiper-button-prev',
        },
        breakpoints: {
            320: {
                spaceBetween: 20,
            },
            993: {
                spaceBetween: 65,
            }
        }
    })
}


$(document).ready(function() {

    sliderAdvantages();
    sliderFeedback();

    // Menu
    $('.burger').click(function () {
        $(this).toggleClass('burger--active')
        $('.header__right').toggleClass('header__right--active')
        $('body').toggleClass('fixed-body')
    })

    // Before - After
    $(".before-after").twentytwenty({
        default_offset_pct: 0.5,
        no_overlay: true,
    });



})